}

.isotop-filter {
    background: transparent;
    flex-wrap: wrap;
    margin: -5px;
    button {
        font-size: 16px;
        width: auto;
        border: 1px solid var(--color-border);
        height: 47px;
        display: inline-block;
        padding: 0 26px;
        border-radius: 500px;
        min-width: 95px;
        text-align: center;
        margin: 5px;
        line-height: 42px;

        @media #{$laptop-device} {
            font-size: 14px;
            padding: 0 10px;
        }

        @media #{$sm-layout} {
            font-size: 14px;
            height: 44px;
            padding: 0 14px;
            line-height: 37px;
        }


        &.is-checked ,
        &:hover ,
        &:focus {
            color: var(--color-primary) !important;
            background: var(--color-primary-alta);
            border-color: var(--color-primary-alta);
        }
    }
}





/*---------------------------
    Filter Exanded  
-----------------------------*/
.default-exp-wrapper{
    border-top: 1px solid var(--color-border);
    .inner {
        display: flex;
        padding: 40px 0;
        justify-content: flex-end;
        margin: -10px;
        flex-wrap: wrap;

        @media #{$lg-layout} {
            justify-content: flex-start;
        }
        @media #{$md-layout} {
            justify-content: start;
        }

        @media #{$sm-layout} {
            justify-content: start;
        }

        .filter-select-option {
            flex-basis: 20%;
            padding: 10px;
            
            @media #{$lg-layout} {
                flex-basis: 33.33%;
            }

            @media #{$md-layout} {
                flex-basis: 50%;
            }

            @media #{$sm-layout} {
                flex-basis: 50%;
            }

            @media #{$large-mobile} {
                flex-basis: 100%;
            }

            .filter-leble {
                display: block;
                font-size: 12px;
                text-transform: uppercase;
                letter-spacing: 0.5px;
                margin-bottom: 10px;
            }
            .nice-select {
                width: 100%;
                padding-left: 20px;
                padding-right: 40px;
                &::after {
                    right: 22px;
                    height: 8px;
                    width: 8px;
                    opacity: 0.5;
                }
                .list {
                    min-width: 100%;
                    max-height: 300px;
                    overflow-y: auto;
                }
            }
        }
    }
}

.default-exp-expand {
    display: none;
}


// tab pgoduct

.product-tab-nav{
    .nav{
        background: var(--background-color-1);
        padding: 10px;
        border-radius: 5px 5px 0 0 ;
        border-right: 1px solid var(--color-border);
        border-left: 1px solid var(--color-border);
        border-top: 1px solid var(--color-border);
        button{
            width: auto;
            &.nav-link{
                padding: 13px 30px;
                margin-right: 20px;
                border-radius: 5px;
                color: var(--color-white);
                border: 1px dashed var(--color-border);
                transition: var(--transition);
                font-size: 16px;
                @media only screen and (max-width: 526px) {
                    margin-bottom: 20px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
                &.active{
                    background: var(--color-primary-alta);
                    border: 1px dashed transparent;
                }
                &:hover{
                    border: 1px dashed var(--color-white);
                    color: var(--color-white);
                }
                &:active{
                    border: 1px dashed transparent;
                    color: var(--color-white);
                }
                &:focus{
                    border: 1px dashed transparent;
                    color: var(--color-white);
                }
            }
        }
    }
}

